@import url('https://fonts.googleapis.com/css2?family=Belanosima:wght@600&family=Roboto+Condensed&family=Roboto+Slab:wght@500&family=Varta&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&family=Satisfy&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bree+Serif&family=Dancing+Script:wght@600&family=Satisfy&display=swap');

* {
    margin: 0px;
    box-sizing: border-box;
}

@mixin display($dis: flex, $justify: center, $align: center, $flex: row, $font-size: 1em) {
    display: $dis;
    justify-content: $justify;
    align-items: $align;
    flex-direction: $flex;
    font-size: $font-size;
}

.border1 {
    height: 100vh;
    // border: 1px solid gold;
    background-color: #191919;
    @include display($flex: column, $justify: flex-end);

    .navbar1 {
        width: 85%;
        height: 16%;
        // border: 1px solid gold;
        position: fixed;
        top: 10px;
        @include display($flex: row, $justify: space-around);

        .left {
            width: 28%;
            height: 95%;
            // border: 1px solid gold;
            @include display($justify: flex-start);
            color: #F1B200;

            .chair {
                margin-left: 30px;
                width: 15%;
                height: 60%;
                // border: 1px solid gold;
                @include display($font-size: 2.2em);
            }

            .furniture {
                margin-left: 10px;
                padding: 5px;
                // border: 1px solid gold;
                @include display($font-size: 1.5em);
                color: aliceblue;
                font-family: 'Roboto Condensed', sans-serif;
                text-transform: capitalize;

            }
        }

        .center {
            width: 42%;
            height: 95%;
            // border: 1px solid gold;
            @include display;

            .H {
                font-size: 1em;
                color: #7C7C7C;
                font-family: 'Roboto Condensed', sans-serif;
                padding: 15px;
                text-transform: capitalize;

                &:hover {
                    color: #F1B200;
                    cursor: pointer;
                }
            }
        }

        .right {
            width: 28%;
            height: 95%;
            // border: 1px solid gold;
            @include display($justify: center);

            .trolly {
                width: 20%;
                height: 85%;
                margin-right: 20px;
                // border: 1px solid gold;
                @include display($font-size: 1.8em);
                color: aliceblue;

            }

            .shop {
                width: 50%;
                height: 50%;
                // border: 1px solid #F1B200;
                border-radius: 30px;
                background-color: #F1B200;
                @include display;
                color: #191919;
                text-transform: capitalize;
                font-weight: 550;
                font-family: 'Roboto Condensed', sans-serif;

                &:hover {
                    color: white;
                    cursor: pointer;
                }
            }
        }
    }


    .box1 {
        width: 90%;
        height: 82%;
        // border: 1px solid gold;
        @include display($justify: space-around);

        .furnitureleft {
            width: 60%;
            height: 100%;
            // border: 1px solid gold;
            @include display($justify: space-around, $flex: column);

            .one {
                width: 90%;
                height: 25%;
                // border: 1px solid gold;
                @include display($justify: center, $flex: column, $align: flex-start);

                .exclusive0 {
                    width: 90%;
                    height: 45%;
                    // border: 1px solid gold;
                    @include display($font-size: 3em, $justify: flex-start);
                    color: #F1B200;
                    font-family: 'Satisfy', cursive;
                    text-transform: capitalize;
                }

                .exclusive1 {
                    width: 90%;
                    height: 45%;
                    // border: 1px solid gold;
                    @include display($font-size: 4.5em,
                        $justify: flex-start);
                    color: #FFFFFF;
                    font-family: 'Bree Serif', serif;
                    text-transform: capitalize;
                }
            }

            .two {
                width: 80%;
                height: 58%;
                // border: 1px solid gold;
                @include display;

                .img1 {
                    width: 83%;
                    height: 95%;
                    // border: 1px solid gold;
                    border-radius: 30px;
                    background-image: url('./images/furniture1.jpg');
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center;
                }

                .forsale {
                    text-transform: capitalize;
                    border-radius: 30px;
                    padding: 15px;
                    background-color: #FFFFFF;
                    @include display($font-size: 1.4em);
                    color: #191919;
                    font-family: 'Roboto Condensed', sans-serif;
                    font-weight: 600;
                    align-self: flex-start;
                    margin-left: -60px;
                    margin-top: -30px;
                }

            }

            .three {
                width: 90%;
                height: 20%;
                // border: 1px solid gold;
                @include display($font-size: 1.4em);

                .arrow0 {
                    width: 35%;
                    height: 95%;
                    // border: 1px solid gold;
                    @include display($font-size: 4.4em);
                    color: #F1B200;
                }

                .arrow1 {
                    width: 60%;
                    height: 95%;
                    // border: 1px solid gold;
                    @include display($font-size: 0.75em);
                    text-transform: uppercase;
                    color: #7C7C7C;
                    font-weight: 520;
                    font-family: 'Roboto Condensed', sans-serif;

                }
            }
        }

        .furnitureright {
            width: 35%;
            height: 95%;
            // border: 1px solid gold;
            @include display($justify: space-evenly, $flex: column);

            .rightone {
                width: 85%;
                height: 28%;
                // border: 1px solid gold;
                @include display($justify: space-evenly, $flex: row);

                .roundarrow {
                    color: #F1B200;
                    font-size: 5em;
                    margin-left: -200px;
                }

                .circle {
                    width: 130px;
                    height: 130px;
                    border-radius: 50%;
                    // border: 1px solid white;
                    @include display();
                    background-image: url('./images/lamp1.jpg');
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center;



                    .num {
                        color: #FFFFFF;
                        font-size: 1.5em;
                        font-weight: 550;
                        text-transform: uppercase;
                        font-family: 'Roboto Condensed', sans-serif;
                        margin-left: -160px;
                        margin-top: 50px;
                    }
                }
            }

            .righttwo {
                width: 85%;
                height: 35%;
                // border: 1px solid gold;
                @include display($justify: space-evenly, $flex: column);

                .words1 {
                    width: 90%;
                    height: 60%;
                    // border: 1px solid gold;
                    @include display;
                    color: #7C7C7C;
                    text-transform: capitalize;
                    text-align: justify;
                    font-family: 'Roboto Condensed', sans-serif;

                }

                .words2 {
                    width: 90%;
                    height: 35%;
                    // border: 1px solid gold;
                    @include display($justify: flex-start);

                    .view {
                        padding: 15px;
                        background-color: #F1B200;
                        color: #191919;
                        font-family: 'Roboto Condensed', sans-serif;
                        border-radius: 30px;
                        text-transform: capitalize;
                        font-weight: 650;
                        letter-spacing: 1px;

                        &:hover {
                            cursor: pointer;
                            color: #FFFFFF;
                        }
                    }
                }
            }

            .rightthree {
                width: 85%;
                height: 35%;
                // border: 1px solid gold;
                @include display($justify: space-evenly, $flex: column);

                .img2 {
                    width: 70%;
                    height: 98%;
                    // border: 1px solid white;
                    border-radius: 20px;
                    background-image: url('./images/furniture2.jpg');
                    background-repeat: no-repeat;
                    background-size: cover;

                }
            }
        }
    }

    .feature {
        width: 90%;
        height: 82%;
        // border: 1px solid gold;
        @include display($flex: column);

        .topfeature {
            width: 85%;
            height: 30%;
            // border: 1px solid gold;
            @include display;
        }

        .topfeature {
            width: 90%;
            height: 30%;
            // border: 1px solid gold;
            @include display($justify: space-between);

            .leftfeature {
                width: 55%;
                height: 95%;
                // border: 1px solid gold;
                @include display($align: flex-start, $justify: space-around, $flex: column);

                .leftfeatop {
                    width: 85%;
                    height: 40%;
                    // border: 1px solid gold;
                    @include display($font-size: 2.3em, $justify: flex-start);
                    text-transform: capitalize;
                    color: #F1B200;
                    font-family: 'Satisfy', cursive;
                }

                .leftfeabottom {
                    width: 95%;
                    height: 55%;
                    // border: 1px solid gold;
                    @include display($font-size: 2.5em,
                        $justify: flex-start);
                    text-transform: capitalize;
                    color: #FFFFFF;
                    font-family: 'Roboto Condensed', sans-serif;
                }
            }

            .rightfeature {
                width: 40%;
                height: 95%;
                // border: 1px solid gold;
                text-transform: capitalize;
                text-align: justify;
                @include display($font-size: 1.2em);
                color: #7C7C7C;
                font-family: 'Roboto Condensed', sans-serif;
            }
        }

        .bottomfeature {
            width: 90%;
            height: 65%;
            // border: 1px solid gold;
            @include display($justify: space-around);

            .quality1 {
                width: 24%;
                height: 80%;
                border: 2px solid gray;
                border-radius: 15px;
                @include display($justify: space-around, $flex: column);



                .qualityone {
                    width: 90%;
                    height: 40%;
                    // border: 1px solid gray;
                    @include display($justify: flex-start, $font-size: 3em);
                    color: #F1B200;
                    margin-left: 10px;

                }

                .qualitytwo {
                    width: 90%;
                    height: 26%;
                    // border: 1px solid gray;
                    @include display($font-size: 1.8em, $justify: flex-start);
                    color: #FFFFFF;
                    text-transform: capitalize;
                    font-family: 'Roboto Condensed', sans-serif;
                }

                .qualitythree {
                    width: 90%;
                    height: 28%;
                    // border: 1px solid gray;
                    @include display($font-size: 0.9em);
                    color: #7C7C7C;
                    font-weight: 520;
                    text-transform: capitalize;
                    text-align: justify;
                    font-family: 'Bree Serif', serif;
                    transition: 2s;
                }

                &:hover {
                    transition: 1s;
                    transform: scale(1.1);
                    box-shadow: 2px 2px black, 4px 4px rgb(81, 77, 77), 6px 6px rgb(28, 27, 27);
                    cursor: pointer;
                }
            }
        }
    }
}